* {
  margin: 0;
  padding: 0;
  font-family: 'Times New Roman', Times, serif;  
}

.hero{
background: rgb(255,255,255);
background: linear-gradient(180deg, rgba(255,255,255,1) 0%, rgba(254,202,202,1) 99%);
max-width: 1440px;
background-position: center;
background-size: cover;
}

.header{
  display: flex;
  flex-direction: row;
  align-items: baseline;
  justify-content: space-between;
  align-items: baseline;
}

.header-left{
  display: flex;
  flex-direction: row;
  padding-top: 50px;
  margin-left: 100px;
}

.header-left h1{
  margin-top: -8px;
}

.header-left img{
  height: 20px;
  width: 20px;
  margin-right: 10px;
}

.header-right{
  display: flex;
  flex-direction: row;
  margin-right: 100px;  
}

.header-right h4{
  margin-left: 20px;
  font-weight: lighter;
}

.header-right .en{
  font-weight:bolder;
  margin-left: 30px;
}

.hero .main{
  margin-top: 100px;
  margin-left: 100px;
}

.hero .main h1{
  font-size: 50px;
  font-weight: lighter;
  margin-top: -5px;
  margin-bottom: 50px;
}

.hero .main h1, .hero .main h4, .hero .main button{
  color:rgb(218, 14, 55);
}

.hero .main button{
  width: 200px;
  height: 50px;
  margin-top: 30px;
  border: 1px solid rgb(218, 14, 55);
  border-radius: 10px;
  background-color: inherit;
  margin-bottom:100px;
}

.hero .main img{
  position:absolute;
  right:0;
  top: 160px;
  max-width: 520px;
}

.container .houses{
  background: url('../src/img/bg-houses.png'), no-repeat;
  background-size: contain;
  max-width: 1440px;
  padding-bottom: 200px;
  margin-bottom: 100px;
  z-index: 100;
}

/* Services section */

.services{
  margin:100px 100px;
  color: blue;
}

.services h1{
  font-size: 50px;
  font-weight: lighter;
  margin-top: -5px;
  margin-bottom: 50px;
  color: blue;
}

.services-text{
  display: flex;
  flex-direction: row;
  justify-content: center;
}

.services-text p{
  width: 400px;
  height: 60px;
  font-weight: bolder;
  background-color: blue;
  color: white;
  margin-right: 10px;
  text-align: center;
  border-radius: 10px;
}

.services-text .text1{
  background-color: blue;
  padding-top: 40px;
}

.services-text .text2{
  background-color: rgb(151, 51, 4);
  padding-top: 30px;
  padding-bottom: 10px;
}

.services-text .text3{
  background-color: rgb(151, 14, 4);
  padding-top: 40px;
}

/* Articles section */

.articles{
  margin: 100px 0;
  background-color: rgb(230, 246, 247);
}

.articles .les{
  margin-left: 100px;
  padding-top: 100px;
  color: blue;
}

.articles h1{
  font-size: 50px;
  font-weight: lighter;
  margin-left: 100px; 
  margin-bottom: 50px;
  color: blue;
}

.articles-content{
  display: flex;
  flex-direction: row;
  margin: 0 100px 0 100px;
  padding-bottom: 150px;

}

.articles-item{
  background-color: white;
  margin-right: 10px;
  padding: 25px;
  border-radius: 10px;
  width: 300px;
}

.articles-item h3{
  margin-bottom: 10px;  
}

.articles-item h6{
  margin-bottom: 25px;  
  font-weight: lighter;
  display: flex;
  flex-direction: row;
}

.articles-item p{
  font-size: 13px;
}

.articles-item li{
  margin-left: 6px;
}

.articles-item a{
  color: black;
  margin-left: -8px;
}

.articles-item .symbol{
  font-size: 20px;
  font-weight: bolder;
}

/* Footer section */

.footer{
  display: flex;
  flex-direction: row;
  align-items: baseline;
  justify-content: space-between;
  align-items: baseline;
  margin-bottom: 30px;
}
.footer-left{
  display: flex;
  flex-direction: row;
  padding-top: 50px;
  margin-left: 100px;
}

.footer-left h1{
  margin-top: -8px;
  font-weight: lighter;
}

.footer-left img{
  height: 20px;
  width: 20px;
  margin-right: 10px;
}

.footer-mid{
  display: flex;
  flex-direction: column;
  gap: 30px 0;
  align-content: space-between;
}

.footer-mid h4{
  font-weight: lighter;
}

.footer-right{
  display: flex;
  flex-direction: column;
  gap: 30px 0;
  margin-right: 150px;
  align-content: space-between;
}

.footer-right p{
  font-size: 14px;
  margin-bottom: 5px;
}


